.demo-card-wide.mdl-card {
  width: 900px;
  margin: 20px; }

.demo-card-wide > .mdl-card__title {
    color: #fff;
    height: 150px;
    background: url('../pics/welcome_card.jpg') center / cover;
    display: inline-block; }

.mdl-card__title-text {
  position: absolute;
  left: 25px;
  top: 30px; }

.demo-card-wide > .mdl-card__menu {
  color: #fff; }

.demo-card-wide > .mdl-card__supporting-text {
  height: 50px; }

.tags {
  position: absolute;
  top: 90px;
  width: 96%;
  display: inline-flex;
  flex-wrap: wrap; }

.mdl-chip {
  margin-right: 10px; }

#info {
  position: absolute;
  top: 84px;
  left: 70%; }

.demo-card-square.mdl-card {
  width: 385px;
  height: 320px; }

.demo-card-square > .mdl-card__title {
  color: #fff;
  background: url('../pics/dog.png') bottom right 15% no-repeat #46B6AC; }
